}

.navigation a {
  position: relative;
}

.navigation a:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--green);
  transition: transform .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.navigation a:hover:before, .navigation a:focus:before {
  transform: scaleX(1);
}

