@media screen and (min-width: 768px) {
  .header-switch {
    width: 47px;
    height: 20px;
    border-radius: 10px;
    display: flex;
    position: relative;
    box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
  }
}

.header-switch input {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: var(--gray-radiobtn-w);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-slider:before {
  content: "";
  height: 17px;
  width: 17px;
  background-color: var(--background-w);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.header-switch input:checked + .header-slider {
  background: linear-gradient(#9bb537, #9bb537);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-switch input:checked + .header-slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.mobile-menu {
  background-color: var(--green);
  height: 100%;
  width: 100%;
  z-index: 999;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu.open {
  transform: translateX(0);
}

.mob-menu-btn-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-btn-close .mob-menu-close {
  transition: transform .3s ease-in-out;
}

.mob-menu-btn-close:hover {
  transform: rotate(45deg);
}

.mob-menu-btn-close:hover .mob-menu-close {
  transform: scale(1.2);
}

.mob-menu-close-icon {
